import instance from './instance'

const getCards = () => {
  return instance("items")
}

const getCard = (id) => {
  return instance("items/" + Number(id))
}

const update = (obj) => {
  return instance.put(`items/${obj.id}`, obj)
}



export const cardsService = {
  getCards,
  getCard,
  update,
}

